@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
body {
  font-family: "Source Sans Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  color: rgb(236, 25, 56);
  font-family: "Oswald", sans-serif;
  font-weight: 700;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  font-size: larger;
}

.navbar {
  width: 100%;
  margin: 0;
  height: auto;
  margin: 0 auto;
  margin-bottom: 40px;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  padding-bottom: 0;
  border-bottom: 3px solid rgb(235, 235, 235);
  background-color: white;
}

@media (min-width: 768px) {
  .navbar {
    padding: 0;
  }
}

.navbar__flex {
  width: 100%;
  text-align: center;
}

@media (min-width: 768px) {
  .navbar__flex {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.navbar__brand {
  color: rgb(236, 25, 56);
  text-decoration: none;
  height: 30px;
  font-size: 25px;
}

@media (min-width: 768px) {
  .navbar__brand {
    display: flex;
    align-items: center;
  }
}

.navbar__brand:hover {
  color: rgb(236, 25, 56);
  text-decoration: none;
}

.navbar__brand span {
  font-style: normal;
}

.navbar__brand-logo {
  height: 50px;
  padding-right: 10px;
}

.navbar__menu {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .navbar__menu {
    width: 50%;
  }
}
.navbar__menu-link {
  color: rgb(60, 60, 67);
  text-decoration: none;
  transform: scale(0.8);
  transition: all 0.2s ease-in-out;
}

@media (min-width: 768px) {
  .navbar__menu-link {
    transform: scale(1);
  }
}

.navbar__menu-link:hover {
  color: rgb(60, 60, 67);
  text-decoration: none;
  font-weight: bold;
  transform: scale(1);
}

@media (min-width: 768px) {
  .navbar__menu-link:hover {
    transform: scale(1.2);
  }
}

.navbar__menu-active {
  height: 80px;
  font-weight: 700;
  padding: 35px 10px 0;
  color: white;
  background-color: rgb(236, 25, 56);
}

.Footer {
  background-color: #282c34;
  color: white;
  margin: 0;
  margin-top: 30px;
  text-align: center;
}

.Footer p {
  margin: 0;
  padding: 20px 0;
}

.Footer span {
  font-style: italic;
  font-weight: bold;
}

.Programa {
  text-align: center;
  margin: 30px;
}

@media (min-width: 768px) {
  .Programa {
    width: 50%;
  }
}

.Programa__image {
  max-width: 200px;
  margin: 10px;
}

.Programa__title {
  font-weight: bold;
}

@media (min-width: 768px) {
  .Programas__flex {
    display: flex;
  }
}

.hero {
  text-align: center;
  padding-bottom: 10px;
}

@media (min-width: 768px) {
  .hero {
    display: flex;
    justify-content: space-around;
  }
}

.hero img {
  align-self: center;
  max-height: 400px;
  padding-bottom: 30px;
  max-width: 100%;
}

h1 {
  text-align: center;
  width: 100%;
}

.Quienes {
  padding-bottom: 80px;
}

@media (min-width: 768px) {
  .Quienes {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    align-items: center;
  }

  .Quienes p {
    max-width: 50%;
  }
}

.Quienes__img {
  max-width: 100%;
}

@media (min-width: 768px) {
  .Quienes__img {
    max-width: 500px;
  }
}
.Quienes span {
  font-weight: bold;
}

.Quienes p {
  color: #212529;
}

.Mision {
  background-color: rgb(236, 25, 56);
  color: white;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 768px) {
  .flex {
    display: flex;
    justify-content: center;
    margin: 0 auto;
  }

  .Mision {
    padding-left: 7%;
    padding-right: 20px;
  }

  .Vision {
    padding-left: 20px;
    padding-right: 7%;
  }
}

.Vision {
  background-color: rgb(237, 236, 237);
  height: 300px;
  display: flex;
  align-items: center;
}

.Valores {
  padding-top: 30px;
}

.Valores h2 {
  margin-bottom: 30px;
}
.Valores h2 span {
  color: rgb(236, 25, 56);
}

.Valores span {
  font-weight: bold;
}

.Valores__valor::before {
  content: ">  ";
  color: rgb(236, 25, 56);
  font-weight: bolder;
}

